'use client'

import { useCallback, useEffect, useMemo, useState } from 'react'

import Alert from '@mui/material/Alert'
import Box from '@mui/material/Box'
import Button from '@mui/material/Button'
import Card from '@mui/material/Card'
import CardContent from '@mui/material/CardContent'
import Chip from '@mui/material/Chip'
import CircularProgress from '@mui/material/CircularProgress'
import MenuItem from '@mui/material/MenuItem'
import Pagination from '@mui/material/Pagination'
import Stack from '@mui/material/Stack'
import Table from '@mui/material/Table'
import TableBody from '@mui/material/TableBody'
import TableCell from '@mui/material/TableCell'
import TableHead from '@mui/material/TableHead'
import TableRow from '@mui/material/TableRow'
import TextField from '@mui/material/TextField'
import Typography from '@mui/material/Typography'

import { aprobarSolicitud, listarSolicitudes, rechazarSolicitud } from '@/api/solicitudes'

const extractRows = payload => {
  if (Array.isArray(payload)) return payload
  if (Array.isArray(payload?.data)) return payload.data
  if (Array.isArray(payload?.rows)) return payload.rows
  if (Array.isArray(payload?.data?.rows)) return payload.data.rows

  return []
}

const extractPagination = payload => {
  const source = payload?.pagination || payload?.data?.pagination

  return {
    page: Number(source?.page || 1),
    pages: Number(source?.pages || 1),
    total: Number(source?.total || extractRows(payload).length)
  }
}

const getEstadoColor = estado => {
  if (estado === 'APROBADO') return 'success'
  if (estado === 'RECHAZADO') return 'error'

  return 'warning'
}

export default function SolicitudesModule() {
  const [solicitudes, setSolicitudes] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const [search, setSearch] = useState('')
  const [estado, setEstado] = useState('')
  const [page, setPage] = useState(1)
  const [pagination, setPagination] = useState({ page: 1, pages: 1, total: 0 })
  const [processingId, setProcessingId] = useState('')

  const loadSolicitudes = useCallback(async () => {
    setLoading(true)
    setError('')

    try {
      const response = await listarSolicitudes({ page, limit: 20, estado, search })
      const rows = extractRows(response)

      rows.sort((a, b) => {
        if (a.estado === 'PENDIENTE' && b.estado !== 'PENDIENTE') return -1
        if (a.estado !== 'PENDIENTE' && b.estado === 'PENDIENTE') return 1

        return 0
      })

      setSolicitudes(rows)
      setPagination(extractPagination(response))
    } catch (err) {
      setError(err.message || 'No se pudo cargar solicitudes.')
    } finally {
      setLoading(false)
    }
  }, [estado, page, search])

  useEffect(() => {
    loadSolicitudes()
  }, [loadSolicitudes])

  useEffect(() => {
    setPage(1)
  }, [estado, search])

  const runAprobacion = async row => {
    setProcessingId(row.id)
    setError('')
    setSuccess('')

    try {
      const interes = Number(row.tasa_variable || 0)

      await aprobarSolicitud(row.id, {
        interes: Number.isFinite(interes) ? Math.round(interes) : 0,
        num_semanas: Number(row.plazo_semanas || 0)
      })

      setSuccess(`Solicitud ${row.id} aprobada.`)
      await loadSolicitudes()
    } catch (err) {
      setError(err.message || 'No se pudo aprobar la solicitud.')
    } finally {
      setProcessingId('')
    }
  }

  const runRechazo = async row => {
    setProcessingId(row.id)
    setError('')
    setSuccess('')

    try {
      await rechazarSolicitud(row.id)
      setSuccess(`Solicitud ${row.id} rechazada.`)
      await loadSolicitudes()
    } catch (err) {
      setError(err.message || 'No se pudo rechazar la solicitud.')
    } finally {
      setProcessingId('')
    }
  }

  const rows = useMemo(() => solicitudes || [], [solicitudes])

  return (
    <Card>
      <CardContent>
        <Stack spacing={3}>
          <Box>
            <Typography variant='h4'>Solicitudes</Typography>
            <Typography color='text.secondary'>Listado y cambio de estado de solicitudes.</Typography>
          </Box>

          <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
            <TextField
              label='Buscar cliente'
              placeholder='Nombre o email'
              value={search}
              onChange={event => setSearch(event.target.value)}
              size='small'
              fullWidth
            />
            <TextField
              select
              label='Estado'
              value={estado}
              onChange={event => setEstado(event.target.value)}
              size='small'
              sx={{ minWidth: 220 }}
            >
              <MenuItem value=''>Todas</MenuItem>
              <MenuItem value='PENDIENTE'>PENDIENTE</MenuItem>
              <MenuItem value='APROBADO'>APROBADO</MenuItem>
              <MenuItem value='RECHAZADO'>RECHAZADO</MenuItem>
            </TextField>
          </Stack>

          {error ? <Alert severity='error'>{error}</Alert> : null}
          {success ? <Alert severity='success'>{success}</Alert> : null}

          {loading ? (
            <Stack alignItems='center' py={8}>
              <CircularProgress size={28} />
            </Stack>
          ) : (
            <Table size='small'>
              <TableHead>
                <TableRow>
                  <TableCell>Cliente</TableCell>
                  <TableCell>Monto</TableCell>
                  <TableCell>Plazo (semanas)</TableCell>
                  <TableCell>Tasa variable (%)</TableCell>
                  <TableCell>Estado</TableCell>
                  <TableCell>Acciones</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map(row => {
                  const isBusy = processingId === row.id
                  const isPendiente = row.estado === 'PENDIENTE'

                  const cliente = row?.cliente
                    ? `${row.cliente?.nombre || ''} ${row.cliente?.apellido || ''}`.trim()
                    : row.cliente_nombre || row.nombre_cliente || '-'

                  return (
                    <TableRow key={row.id} hover>
                      <TableCell>{cliente || '-'}</TableCell>
                      <TableCell>{row.monto_solicitado ?? '-'}</TableCell>
                      <TableCell>{row.plazo_semanas ?? '-'}</TableCell>
                      <TableCell>{row.tasa_variable ?? '-'}</TableCell>
                      <TableCell>
                        <Chip
                          label={row.estado || '-'}
                          color={getEstadoColor(row.estado)}
                          size='small'
                          variant='outlined'
                        />
                      </TableCell>
                      <TableCell>
                        <Stack direction='row' spacing={1}>
                          <Button
                            variant='contained'
                            color='success'
                            size='small'
                            disabled={!isPendiente || isBusy}
                            onClick={() => runAprobacion(row)}
                          >
                            {isBusy ? 'Aprobando...' : 'Aprobar'}
                          </Button>
                          <Button
                            variant='outlined'
                            color='error'
                            size='small'
                            disabled={!isPendiente || isBusy}
                            onClick={() => runRechazo(row)}
                          >
                            {isBusy ? 'Rechazando...' : 'Rechazar'}
                          </Button>
                        </Stack>
                      </TableCell>
                    </TableRow>
                  )
                })}
                {rows.length === 0 ? (
                  <TableRow>
                    <TableCell colSpan={6} align='center'>
                      Sin resultados
                    </TableCell>
                  </TableRow>
                ) : null}
              </TableBody>
            </Table>
          )}

          <Stack direction='row' justifyContent='space-between' alignItems='center'>
            <Typography color='text.secondary'>Total: {pagination.total}</Typography>
            <Pagination
              page={pagination.page}
              count={Math.max(pagination.pages, 1)}
              onChange={(_, value) => setPage(value)}
              size='small'
              color='primary'
            />
          </Stack>
        </Stack>
      </CardContent>
    </Card>
  )
}
